                <div class="modal-body">
                  <h3 class="text-uppercase">專家斥名冊審批欠透明</h3>
                  <p class="item-intro text-muted">信報</p>
                  <p class="item-intro text-muted">2019 年 10 月 3 日</p>
                  <p>「所謂成本效益，其實只是看成本。」公共衞生研究社召集人陳盈批評，香港沒有獨立的藥事機關，藥事管理委員會亦是醫管局的一部分，「用藥的是它，選藥的也是它。」她舉例，英國的藥物審批由獨立於國民保健署（NHS）、名為NICE的組織負責，確保公正之餘，亦會清楚解釋以什麼準則去量度應否納入藥物，「可以把人命換算成金額，或者藥物實際的臨床作用等。但在香港，公眾根本無從得知怎樣的藥才合乎醫管局的『成本效益』。」</p>
                  <a href="https://www1.hkej.com/dailynews/article/id/2266395/" target="_blank"><button class="btn btn-primary" type="button">
                      閱讀全文（信報）</button></a>
                  <button class="btn btn-secondary" data-dismiss="modal" type="button">
                    <i class="fas fa-times"></i>
                    返回</button>
                </div>
