    <div class="portfolio-modal modal fade" id="interview20190901" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="close-modal" data-dismiss="modal">
            <div class="lr">
              <div class="rl"></div>
            </div>
          </div>
          <div class="container">
            <div class="row">
              <div class="col-lg-8 mx-auto">
                <div class="modal-body">
                  <h3 class="text-uppercase">未來城市：醫療改革 停滯30年——專訪哈佛報告學者蕭慶倫</h3>
                  <p class="item-intro text-muted">明報</p>
                  <p class="item-intro text-muted">2019 年 9 月 1 日</p>
                  <p>「香港政府這三十年真係咩都無做，講三十年都益了它。」公共衛生研究社成員呂詩婷語帶憤慨。香港醫療制度自一九九○年醫院管理局成立之後，再沒有重大改變。一九九三年政府發表《促進健康諮詢文件》（文件以彩虹為封面，俗稱「彩虹報告書」），指出當時所有公共衛生服務都由政府大量補貼，政府補貼大部分病人八成或以上醫療開支，因此如果不採取行動，相信本地醫療服務質素將會惡化，故此首次提出設立強制供款式醫療保險計劃的建議。</p>
                  <a href="https://news.mingpao.com/pns/%E5%89%AF%E5%88%8A/article/20190901/s00005/1567277275985/%E6%9C%AA%E4%BE%86%E5%9F%8E%E5%B8%82-%E9%86%AB%E7%99%82%E6%94%B9%E9%9D%A9-%E5%81%9C%E6%BB%AF30%E5%B9%B4-%E5%B0%88%E8%A8%AA%E5%93%88%E4%BD%9B%E5%A0%B1%E5%91%8A%E5%AD%B8%E8%80%85%E8%95%AD%E6%85%B6%E5%80%AB" target="_blank"><button class="btn btn-primary" type="button">
                      閱讀全文（明報）</button></a>
                  <button class="btn btn-secondary" data-dismiss="modal" type="button">
                    <i class="fas fa-times"></i>
                    返回</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  
